@charset "UTF-8";
:root {
  /**
  * Theme -- DARK
  */
  --dark__bkg-color: #1c1c1c;
  --dark__text-color: #ccc;
  --dark__color-primary: #ff2929;
  --dark__color-secondary: #ffc845;
  --dark__font-regular-weight: 300;
  --dark__font-bold-weight: 500;
  /**
  * Theme -- LIGHT
  */
  --light__bkg-color: #fffafa;
  --light__text-color: #545454;
  --light__color-primary: #9a1818;
  --light__color-secondary: #ffc845;
  --light__font-regular-weight: 400;
  --light__font-bold-weight: 700;
  --effect__transition: 0.4s ease-in-out;
  --font__body: "Inter", Arial, Helvetica, sans-serif;
}

/**
 * --------------------------------------------------------------------
 * DARK THEME
 * --------------------------------------------------------------------
 */
/**
 * --------------------------------------------------------------------
 * LIGHT THEME
 * --------------------------------------------------------------------
 */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 62.5%;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
}

body {
  --color__body-text: var(--light__text-color);
  --color__body-background: var(--light__bkg-color);
  --color__link-body: var(--color__body-text);
  --color__link-body-underline: var(--color__body-text);
  --color__link-body-hover: var(--light__color-primary);
  --color__link-body-underline-hover: var(--light__color-secondary);
  --font__regular-weight: var(--light__font-regular-weight);
  --font__bold-weight: var(--light__font-bold-weight);
}

body.dark-theme {
  --color__body-text: var(--dark__text-color);
  --color__body-background: var(--dark__bkg-color);
  --color__link-body: var(--color__body-text);
  --color__link-body-underline: var(--color__body-text);
  --color__link-body-hover: var(--dark__color-primary);
  --color__link-body-underline-hover: var(--dark__color-secondary);
  --font__regular-weight: var(--dark__font-regular-weight);
  --font__bold-weight: var(--dark__font-bold-weight);
}

@media (prefers-color-scheme: dark) {
  body {
    --color__body-text: var(--dark__text-color);
    --color__body-background: var(--dark__bkg-color);
    --color__link-body: var(--color__body-text);
    --color__link-body-underline: var(--color__body-text);
    --color__link-body-hover: var(--dark__color-primary);
    --color__link-body-underline-hover: var(--dark__color-secondary);
    --font__regular-weight: var(--dark__font-regular-weight);
    --font__bold-weight: var(--dark__font-bold-weight);
  }

  body.light-theme {
    --color__body-text: var(--light__text-color);
    --color__body-background: var(--light__bkg-color);
    --color__link-body: var(--color__body-text);
    --color__link-body-underline: var(--color__body-text);
    --color__link-body-hover: var(--light__color-primary);
    --color__link-body-underline-hover: var(--light__color-secondary);
    --font__regular-weight: var(--light__font-regular-weight);
    --font__bold-weight: var(--light__font-bold-weight);
  }
}
::selection {
  background: #ffc845;
  color: #000;
}

* {
  font-family: var(--font__body);
}

body {
  background: var(--color__body-background);
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  font-weight: var(--font__regular-weight);
  color: var(--color__body-text);
  overflow-x: hidden;
}

main {
  display: grid;
  grid-template-columns: 1fr min(45ch, 100%) 1fr;
  max-width: 90%;
  margin: 0 auto 3em auto;
}
main > * {
  grid-column: 2;
}

p {
  margin: 1.5rem 0;
}

strong {
  font-weight: var(--font__bold-weight);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: var(--font__bold-weight);
}

h1 {
  font-size: clamp(3rem, 4.5vw, 6rem);
  line-height: 1.25;
  margin: 0.75em 0;
}

a {
  color: var(--color__link-body);
  text-decoration-color: var(--color__link-body-underline);
  text-decoration-thickness: 0.125em;
  text-underline-offset: 0.25em;
  transition: var(--effect__transition);
}
a:hover, a:focus {
  color: var(--color__link-body-hover);
  text-decoration-color: var(--color__link-body-underline-hover);
}

a[href^="mailto:"]:after,
a[target=_blank]:after {
  display: inline-block;
  position: relative;
  top: 1px;
  left: 2px;
  content: "â†‘";
  font-size: 90%;
  color: inherit;
  transform: rotate(45deg);
  width: 1em;
  height: 1em;
}